import { FaMagnifyingGlass } from "react-icons/fa6";
import { HiMagnifyingGlass } from "react-icons/hi2";
import { useTranslation, Trans } from 'react-i18next';

function SearchBar(){
    const { t, i18n } = useTranslation();
    return (
        // className="my-4 flex flex-row content-center items-center">
        <div className="table table-auto">
            <div className="border-2 border-e-0 rounded-s-md border-primary p-1 table-cell align-middle">
                <HiMagnifyingGlass />  
            </div>
            <input 
            className="p-1 table-cell border-2 rounded-e-md border-primary shadow-sm"
            type="text" size="40" placeholder={t("search")}/>
        </div>
    )
}
export default SearchBar