import { MdKeyboardArrowLeft, MdKeyboardArrowRight, MdKeyboardArrowUp} from "react-icons/md";
import { useTranslation, Trans } from 'react-i18next';


function Navigation({pagenum, setPagenum}) {
    const { t, i18n } = useTranslation();

    function handleNext(){
        if(pagenum <= 82){
            setPagenum(pagenum+ 1)
        }
    }
    function handlePrev(){
        if (pagenum !== 0) { 
            setPagenum(pagenum -1)
        }
    }
    function handleTOC(){
        setPagenum(0)
    }
let classname_continer = "flex flex-row-reverse justify-end mb-2 "
let classname_button = "underline me-7 my-2 flex items-center "


  return (
    <div className={classname_continer}>
        <button className={classname_button +((pagenum === 83)? " text-mygreyedout " : "text-primary ")} onClick={handleNext}>
             <span>{t("next")}</span>
             { i18n.resolvedLanguage==="en"? <MdKeyboardArrowRight />: <MdKeyboardArrowLeft />}
        </button>
        <button className={classname_button + ((pagenum === 0)? " no-underline text-mygreyedout " : "text-primary ")} onClick={handleTOC}>
             <span>{t("toc")}</span><MdKeyboardArrowUp />
        </button>
        <button className={classname_button + ((pagenum === 0)? " no-underline text-mygreyedout " : "text-primary ")} onClick={handlePrev}>
            { i18n.resolvedLanguage==="ar"? <MdKeyboardArrowRight />: <MdKeyboardArrowLeft />}
            <span>{t("previous")}</span>
        </button>
    </div>
  )
}

export default Navigation