import { useState } from 'react';

import TOC from './TOC.js'
import Page from './Page.js'
import Navigation from './Navigation.js'
import TranslateAllCheckbox from './TranslateAllCheckbox.js';

function ReadPage({data, style}) {
    const [pagenum, setPagenum] = useState(0)
    const [translateAll , setTranslateAll] = useState(false)
    
    console.log(translateAll + " at parent ")

    return (
        <div style={style}>
            <TranslateAllCheckbox 
            translateAll={translateAll} 
            setTranslateAll={setTranslateAll}
             />
            <div className="flex justify-between">
                <Navigation pagenum={pagenum} setPagenum={setPagenum}/>
            </div>
            {pagenum===0?
                <TOC pagenum={pagenum} setPagenum={setPagenum} data={data}/>:
            <Page translateAll={translateAll} pagenum={pagenum} data={data}/>}
            <Navigation pagenum={pagenum} setPagenum={setPagenum}/>
        </div>
    )
}

export default ReadPage