import i18n from "i18next";
import { initReactI18next } from "react-i18next";

// the translations
// (tip move them in a JSON file and import them,
// or even better, manage them separated from your code: https://react.i18next.com/guides/multiple-translation-files)
const resources = {
  ar: {
    translation: {
        "site_title": "التعليم المسيحي للكنيسة الكاثوليكية (الكاتيكزم/الكاتشيزم)",
        "read": "إقرا",
        "topics": "بالموضوعات",
        "search": "ابحث",
        "translate_all_checkbox":{
            "part1": "اضغط على أي فقرة لعرض الترجمة الانجليزية تحتها،",
            "part2": " أو ترجم كل الفقرات",
        },
        "next": "التالي",
        "toc": "الفهرس",
        "previous": "السابق",
    }
  },
  en: {
    translation: {
        "site_title": "Catechism of the Catholic Church",
        "read": "Read",
        "topics": "Topics",
        "search": "Search",
        "translate_all_checkbox":{
            "part1": "Press on any paragraph to view its Arabic translation below it,",
            "part2": "or view all translations",
        },
        "next": "next",
        "toc": "TOC",
        "previous": "previous",

    }
  }
};

i18n
  .use(initReactI18next) // passes i18n down to react-i18next
  .init({
    resources,
    lng: "ar", // language to use, more information here: https://www.i18next.com/overview/configuration-options#languages-namespaces-resources
    // you can use the i18n.changeLanguage function to change the language manually: https://www.i18next.com/overview/api#changelanguage
    // if you're using a language detector, do not define the lng option
    fallbackLng: 'ar',
    interpolation: {
      escapeValue: false // react already safes from xss
    }
  });

  export default i18n;