import { useState } from 'react';
import { useTranslation, Trans } from 'react-i18next';
import { MdExpandMore } from "react-icons/md";
import { MdExpandLess } from "react-icons/md";

function Paragraph({translateAll, pnum, data}) {
    const [ t, i18n ] = useTranslation();
    const [translate, setTranslate] = useState(false)

  let text = "";
  const isArabic = i18n.resolvedLanguage==="ar"
  const isEnglish = i18n.resolvedLanguage==="en"
  const translate_p = translateAll || translate

  function renderArabic(){
    text = data.arabic[pnum].data
      return(
        <div dir="rtl" className='mb-2'>
          <span className = "text-myred">{pnum}-</span><span>&nbsp;</span>
          <span>{text}</span>
        </div>
      )
  }
  function renderEnglish(){
    text = data.english[pnum].data.text
    return(
      <div dir="ltr" className='mb-2'>
        <span className = "text-myred">{pnum}-</span><span>&nbsp;</span>
        <span>{text}</span>

      </div>
    )
  }

  function renderTranslation(){
    if (translate_p){
      if(isArabic){
        return renderEnglish()
      } else {
        return renderArabic()
      }
    }
  }

  function handle_click(){
    setTranslate(!translate)
  }

  // add read the catechism at
  return (
    <div key={pnum} className="border-2 cursor-pointer" onClick={handle_click}>
      <div id="main" className="">
        {isArabic? renderArabic(): renderEnglish()}
      </div>
      <div id="translation">
          {renderTranslation()}
      </div>
    </div>

  )
}

export default Paragraph