function PageButton({id, activePage, setActivePage, children}){

    //using activePage we can style selected button
    // add visual impact for button on click
    // use low three point garadient 
    // add low border for selected 

    function handle_click(){
        if (id===0){
            setActivePage(0)
        } else if (id===1) {
            setActivePage(1)
        } else {
            setActivePage(2)
        }
    }

    //not selected 3am
    let classname ="border-b border-b-gray-500 transition-colors cursor-pointer text-center bg-mybuttonbg min-w-14 p-2 shadow-sm "
    //selected 3am
    if (id === activePage){
        classname ="transition-colors cursor-pointer text-center bg-mybuttonselected text-white border-b-4 border-b-mybuttonselectedborder min-w-14 p-2 shadow-sm " 
    } 


    if (id===0){
        // ekra2 specific
        classname += " rounded-ts-md border-e border-e-background "
        if (id === activePage){
            //selected ekra2 specific
            classname += ""
        }
    } else if (id===1) {
        // elmawdo3at specific
        classname += " border-e  border-e-background"
        if (id === activePage){
            //selected elmawdo3at specific
            classname += ""
        }
    } else {
        // eb7as specific
        classname += " rounded-te-md "
        if (id === activePage){
            //selected eb7as specific
            classname += " "
        }
    }
    

    return (
        <div className={classname} onClick={() => handle_click()}>
            {children}
        </div>
    )
}
export default PageButton 