import SearchPage from './SearchPage.js'
import TopicsPage from './TopicsPage.js'
import ReadPage from "./ReadPage.js"

const show = {
    display: "initial"
}
const hide = {
    display: "none",
}
 
function Pager({activePage, data}){
    return(
        <div className="min-h-64 mb-5 pt-3 border-b-2 border-gray-400">
            <ReadPage data={data} style={activePage===0? show: hide}/>
            <TopicsPage data={data} style={activePage===1? show: hide}/>
            <SearchPage data={data} style={activePage===2? show: hide}/>
        </div>
        )
}

export default Pager