import React from 'react'
import { useTranslation, Trans } from 'react-i18next';

function Parent({parent, onClick, toc}) {
    const { t, i18n } = useTranslation();

     let parent_class_name = " w-max"
    if(toc) {parent_class_name += " cursor-pointer "}
    switch(parent.absolute_level) {
        case 1: //PART
            if(toc){
                parent_class_name += " text-lg font-bold underline "
            } else {
                parent_class_name += " text-lg font-bold underline "
            }
        break;
        case 2: //SECTION
            if(toc){
                parent_class_name += " text-lg font-bold text-mybrown "
            } else {
                parent_class_name += " text-lg font-bold text-mybrown "
            }
        break;
        case 3: //CHAPTER
            if(toc){
                parent_class_name += " text-lg font-bold text-mygreen "
            } else {
                parent_class_name += " text-lg font-bold text-mygreen "
            }
        break;
        case 4: //ARTICLE
            if(toc){
                parent_class_name += " text-mypurple " 
            } else {
                parent_class_name += " text-lg font-bold text-mypurple " 
            }
        break;
        case 5: //PARAGRAPH
            if(toc){
                parent_class_name += " text-mycomplementary "
            } else {
                parent_class_name += " text-lg font-bold text-mycomplementary "
            }
        break;
        case 6: //NUMBERED
            if(toc){
                parent_class_name += " text-myred "
            } else {
                parent_class_name += "text-lg font-bold text-myred "
            }
        break;
        case 7: //paragraphs header (not rendered in toc)
            parent_class_name += " text-myblue "
        break;
    }

  function calc_indentation(entry){
    const indentation_level = entry.absolute_level-1 // 1 is not indented so its level 0
    const string='  '
    return string.repeat(indentation_level)
    } 
    let name = "name"
    if (i18n.resolvedLanguage==="ar"){
        name = "arabicName"
    }
    return (
        <div onClick={onClick} key={parent.key} className={toc? "": "my-1"}>
            {toc && <span style={{whiteSpace: 'pre-wrap'}}>{calc_indentation(parent)}</span>}
            <span className={parent_class_name}>{parent[name]}</span>
        </div>
    )
}

export default Parent