import Title from './Title.js'
import SearchBar from './SearchBar.js'
import LangButtons from './LangButtons.js'
import PageButtons from "./PageButtons.js"

function Header({activePage, setActivePage}) {
  return (
    <header className="flex flex-col items-center justify-center justify-items-center">
      <Title />
      <LangButtons />
      <SearchBar />
      <PageButtons activePage={activePage} setActivePage={setActivePage}/>
    </header>
  )
}
 
export default Header