import Parent from "./Parent.js"
import {useEffect, useRef} from "react"

function TOC({pagenum, setPagenum, data}) {

    const prevpagenum = useRef(0)
    useEffect(() => {
        if (prevpagenum !== pagenum){
           window.scrollTo({ top: 0, left: 0, behavior: 'smooth' })
        }
        return () => {
        // old state
            prevpagenum.current = pagenum
        };

    },pagenum) 

    function handleOnClick(entry, event){
        event.preventDefault()
        // hena hanset el pnum h7seb el page num mn el data wel entry
        const start = entry.range[0]
        for(let i = 1; i < data.pages.length; i++){
                if (start >= data.pages[i][0] && start <= data.pages[i][1]){
                    setPagenum(i)
                    break;
                }
        }
    } 

    const content = data.toc.map(entry => {
        if (entry.absolute_level < 7){
        return(
        <div key={entry.key}> 
          <Parent parent={entry} 
            onClick={(event) => handleOnClick(entry, event)} 
            toc={true}/>
        </div>)
        } else return false 
    })

    return (
        <>
        {content}
        </>
    )
}

export default TOC