import LangButton from "./LangButton"
import { useTranslation, Trans } from 'react-i18next';

function LangButtons() {
  const { t, i18n } = useTranslation();

  function handle_click_ar(){
    i18n.changeLanguage("ar")
  }

  function handle_click_en(){
    i18n.changeLanguage("en")
  }

  return (
    <div dir="rtl" className='m-4 flex flex-row gap-2 border-2 bg-mylangbg rounded-md'>
      <LangButton lang="ar" selected={i18n.resolvedLanguage} onClick={handle_click_ar}>english</LangButton>
      <LangButton lang="en" selected={i18n.resolvedLanguage} onClick={handle_click_en}>arabic</LangButton>
    </div>
      )
}

export default LangButtons