import { useState } from 'react';
import { LuArrowDownWideNarrow } from "react-icons/lu";
import { useTranslation, Trans } from 'react-i18next';


function TranslateAllCheckbox({translateAll, setTranslateAll}) {
  const { t, i18n } = useTranslation();
  const [checked, setChecked] = useState(false);

  const handleChange = () => {
    setTranslateAll(!translateAll)
    setChecked(!checked);

  };

  return (
    <div className="my-2 flex flex-wrap justify-start  justify-items-start items-center content-center">
            <span className='text-sm'>{t('translate_all_checkbox.part1')}</span>
            <span>&nbsp;</span>
            <label className='contents'>          
            <span className='text-primary text-sm font-bold'>{t('translate_all_checkbox.part2')}</span>
            <span>&nbsp;</span>
            <LuArrowDownWideNarrow />
            <span>&nbsp;</span>
            <input
            type="checkbox"
            checked={checked}
            onChange={handleChange}
            className='rounded size-5 accent-emerald-200'
          />
          </label>
    </div>
  )
}

export default TranslateAllCheckbox